"use client";

import { useState } from "react";

interface PackageSearchProps {
  onFilter: (filters: {
    destination: string;
    date: string;
    minPrice: number;
    maxPrice: number;
  }) => void;
}

export default function PackageSearch({ onFilter }: PackageSearchProps) {
  const [destination, setDestination] = useState("");
  const [date, setDate] = useState("");
  const [minPrice, setMinPrice] = useState("");
  const [maxPrice, setMaxPrice] = useState("");

  const handleSearch = () => {
    onFilter({
      destination,
      date,
      minPrice: Number(minPrice) || 0,
      maxPrice: Number(maxPrice) || Infinity,
    });
  };

  return (
    <div className="bg-white shadow-md rounded-lg p-5 mb-8 flex flex-wrap gap-4 items-center justify-between">
      <input
        type="text"
        placeholder="Destination"
        value={destination}
        onChange={(e) => setDestination(e.target.value)}
        className="border rounded px-3 py-2 w-40"
      />
      <input
        type="date"
        value={date}
        onChange={(e) => setDate(e.target.value)}
        className="border rounded px-3 py-2"
      />
      <input
        type="number"
        placeholder="Min Price"
        value={minPrice}
        onChange={(e) => setMinPrice(e.target.value)}
        className="border rounded px-3 py-2 w-28"
      />
      <input
        type="number"
        placeholder="Max Price"
        value={maxPrice}
        onChange={(e) => setMaxPrice(e.target.value)}
        className="border rounded px-3 py-2 w-28"
      />
      <button
        onClick={handleSearch}
        className="bg-sky-600 text-white px-5 py-2 rounded hover:bg-sky-700 transition"
      >
        Search
      </button>
    </div>
  );
}
