import Image from "next/image";
import { TourPackage } from "../data/tourPackages";

export default function PackageCard({ pkg }: { pkg: TourPackage }) {
  return (
    <div className="bg-white rounded-lg shadow-md overflow-hidden hover:shadow-xl transition">
      <Image
        src={pkg.image}
        alt={pkg.title}
        width={400}
        height={250}
        className="w-full h-48 object-cover"
      />
      <div className="p-5">
        <h3 className="text-lg font-semibold text-sky-700">{pkg.title}</h3>
        <p className="text-gray-600 text-sm mt-1">{pkg.destination}</p>
        <p className="text-gray-700 mt-2">{pkg.description}</p>
        <div className="flex justify-between items-center mt-4 text-sm text-gray-700">
          <span>üóìÔ∏è {pkg.departureDate}</span>
          <span>üí∞ ${pkg.price}</span>
        </div>
      </div>
    </div>
  );
}
