"use client";

import { useState } from "react";
import { tourPackages, TourPackage } from "../data/tourPackages";
import PackageCard from "../components/PackageCard";
import PackageSearch from "../components/PackageSearch";

export default function PackagesPage() {
  const [filtered, setFiltered] = useState<TourPackage[]>(tourPackages);

  const handleFilter = (filters: {
    destination: string;
    date: string;
    minPrice: number;
    maxPrice: number;
  }) => {
    const result = tourPackages.filter((pkg) => {
      const matchesDestination = pkg.destination
        .toLowerCase()
        .includes(filters.destination.toLowerCase());
      const matchesDate =
        !filters.date || pkg.departureDate >= filters.date;
      const matchesPrice =
        pkg.price >= filters.minPrice && pkg.price <= filters.maxPrice;
      return matchesDestination && matchesDate && matchesPrice;
    });
    setFiltered(result);
  };

  return (
    <section className="max-w-7xl mx-auto px-6 py-12">
      <h1 className="text-3xl font-bold text-sky-700 text-center mb-8">
        Explore Our Tour Packages
      </h1>
      <PackageSearch onFilter={handleFilter} />
      {filtered.length === 0 ? (
        <p className="text-center text-gray-500 mt-10">
          No packages found. Try adjusting your filters.
        </p>
      ) : (
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8">
          {filtered.map((pkg) => (
            <PackageCard key={pkg.id} pkg={pkg} />
          ))}
        </div>
      )}
    </section>
  );
}
